import React, { useEffect, useState } from 'react';
import { Link } from 'react-scroll';

const Header = () => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  const [scrolled, setScrolled] = useState(false);

  const handleScroll = () => {
    const offset = window.scrollY;
    if (offset > 200) {
      setScrolled(true);
    } else {
      setScrolled(false);
    }
  };

  const resize = () => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    window.addEventListener('resize', resize);
  }, []);

  const navbarClasses = ['navbar'];
  if (scrolled) {
    navbarClasses.push('scrolled');
  }

  const navclasses = ['nav'];
  if (!isDesktop && isMobile) {
    navclasses.push('mini');
  }

  return (
    <header id="hometop" className={navbarClasses.join(' ')}>
      <div className="logo">
        <span className="logo">
          <Link to="hero" smooth duration={500}>
            <span className="alt"> SARTHAK MARWAHA</span>
          </Link>
        </span>
      </div>

      <nav className="navigation">
        <span className={navclasses.join(' ')}>
          <Link to="about" offset={-100} smooth duration={500}>
            <div className="item text-color-alt"> About</div>
          </Link>
          <Link to="Projgallery" offset={-100} smooth duration={500}>
            <div className="item text-color-alt">Projects</div>
          </Link>
          <Link to="contact" offset={-100} smooth duration={500}>
            <div className="item text-color-alt">Contact</div>
          </Link>
        </span>
      </nav>
    </header>
  );
};

export default Header;
